* {
  margin: 0;
  box-sizing: border-box;
  text-align: center;
}

.fade-in-section {
  opacity: 0;
  transform: translateY(20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 1.2s ease-out;
  will-change: opacity, visibility;
}
.fade-in-section.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}


/* Comes From Above */
.fade-in-section2 {
  opacity: 0;
  transform: translateY(-20vh);
  visibility: hidden;
  transition: opacity 0.2s ease-out, transform 1.2s ease-out;
  will-change: opacity, visibility;
}

.fade-in-section2.is-visible {
opacity: 1;
transform: none;
visibility: visible;
}

.fade-in-section3 {
opacity: 0;
transform: translateX(20vw);
visibility: hidden;
transition: opacity 0.2s ease-out, transform .4s ease-out;
will-change: opacity, visibility;
}
.fade-in-section3.is-visible {
opacity: 1;
transform: none;
visibility: visible;
}


body {
  overflow: overlay;
}

::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-thumb {
  background: goldenrod;
}

::-webkit-scrollbar-track {
  background: #121212;
}